.shares {

  .site-width {
    @include breakpoint($mq-tablet) {
      padding: 0;
    }
  }
  div.slide {
    img {
      min-width: 60%;
    }
  }

  @include breakpoint($mq-tablet) {
    div.slide {
      img {
        min-width: 60%;
        left: 40%;
      }
    }
  }

  @include breakpoint($mq-mobile) {
    .shares-slider {
      display: none;
    }
  }

  &-article {
    width: 40%;
    float: left;
    padding: 0;

    @include breakpoint($mq-mobile) {
      width: 100%;
    }

    .over-slider-wrap {
      padding: 135px 30px 50px;
      background: $clr-white;
      position: relative;
      z-index: 10;
      &:last-child {
        padding: 45px 30px 100px;
      }
      @include breakpoint($mq-mobile) {
        padding: 30px 15px 30px;
        &:last-child {
          padding: 30px 15px 30px;
        }
      }
    }

    h2 {
      font-size: 30px;
      font-family: $fontOS;
      color: $bg-dark;
      font-weight: bold;
      margin-bottom: 18px;
    }
  }
  &-top,
  &-finish {
    font-family: $fontOS;
    font-size: 14px;
    font-weight: bold;
    color: $clr-red;
    margin-bottom: 12px;
    display: inline-block;
  }
  &-top {
    color: $clr-red;
  }
  &-finish {
    color: $bg-dark;
    margin-bottom: 55px;
  }
  &-separator {
  }

  &-message {
    font-family: $fontOS;
    font-size: 16px;
    font-weight: 300;
    line-height: 1.2;
    margin-bottom: 40px;
    padding: 0 30px;
    p {
      margin-bottom: 15px;
    }
    @include breakpoint($mq-mobile) {
      padding: 0 15px;
    }
  }

  .push-it {
    height: 68px;
    padding-left: 50px;
    font-size: 14px;
    color: $clr-red;
    font-weight: bold;
    font-family: $fontOS;
    display: flex;
    align-items: center;
    @include breakpoint($mq-tablet) {
      padding-left: 20px;
      height: 50px;
    }
  }

}