.franchising {
  background: $clr-red;
  padding: 135px 0 120px;

  @include breakpoint($mq-mobile) {
    padding: 25px 0 25px;
  }

  &__heading {
    width: calc(100% + 30px);
    margin-left: -70px;
    background: $bg-grey;
    height: 200px;
    color: $clr-white;
    padding-left: 70px;
    position: relative;
    z-index: 5;

    @include breakpoint($mq-mobile) {
      width: calc(100% + 30px);
      margin-left: -15px;
      padding-left: 30px;
      height: 120px;
    }

    h2 {
      font-family: $font;
      font-size: 165px;
      font-weight: bold;
      margin-top: -60px;
      position: relative;
      display: inline-block;
      @include breakpoint($mq-mobile) {
        font-size: 36px;
        margin-top: -13px;
      }
    }
    p {
      display: block;
      padding-left: 265px;
      color: $clr-white;
      padding-top: 20px;
      font-family: $font;
      font-size: 14px;
      @include breakpoint($mq-mobile) {
        font-size: 12px;
        padding-left: 0;
        width: calc(100% - 30px);
      }
    }
  }
  
  &__content {
    padding: 110px 70px 30px 100px;
    background: $clr-white;
    position: relative;
    margin-top: -50px;

    @include breakpoint($mq-mobile) {
      padding: 90px 15px 0;
    }

    &:after {
      content: '';
      width: 100%;
      clear: both;
      display: block;
    }

    .col {
      width: 40%;
      padding-right: 10%;
      float: left;
      margin-bottom: 70px;

      @include breakpoint($mq-mobile) {
        width: 100%;
        padding: 0;
        margin-bottom: 30px;
      }

      h3 {
        font-family: $font;
        color: $clr-red;
        font-size: 30px;
        font-weight: bold;
        display: inline-block;
        width: 100%;
        margin-bottom: 60px;
        @include breakpoint($mq-mobile) {
          font-size: 16px;
          margin-bottom: 20px;
          line-height: 1.5;
        }

        span {
          display: inline-block;
          width: 100%;
          font-size: 24px;
          @include breakpoint($mq-mobile) {
            font-size: 14px;
          }
        }
      }

      p {
        font-size: 14px;
        font-family: $font;
        color: $clr-white-grey;
        @include breakpoint($mq-mobile) {
          font-size: 12px;
        }
      }

      ul {
        li {
          text-transform: uppercase;
          font-size: 14px;
          font-family: $font;
          color: $clr-white-grey;
          position: relative;
          margin-bottom: 20px;
          display: inline-block;
          padding-left: 38px;
          @include breakpoint($mq-mobile) {
            padding-left: 20px;
            font-size: 12px;
            width: 100%;
            margin-bottom: 10px;
          }

          &:before {
            content: '';
            display: block;
            position: absolute;
            left: 0;
            top: 3px;
            width: 8px;
            height: 8px;
            background: $clr-red;
            @include breakpoint($mq-mobile) {
              width: 5px;
              height: 5px;
              top: 2px;
            }
          }
        }
      }

      &:first-child {
        width: 60%;
        padding-right: 22%;
        @include breakpoint($mq-mobile) {
          width: 100%;
          padding: 0;
        }
      }
    }
  }

  .questions {
    width: 60%;
    font-family: $font;
    font-size: 30px;
    font-weight: bold;
    color: $bg-grey;
    line-height: 1.5;
    float: left;
    @include breakpoint($mq-mobile) {
      width: 100%;
      font-size: 18px;
    }

    span.phone {
      display: inline-block;
      width: 100%;
      color: $clr-red;
    }
  }

  &__action {
    float: right;
    width: 40%;
    text-align: center;
    position: relative;
    margin-top: -13px;
    display: flex;
    flex-direction: column;
    align-items: center;
    @include breakpoint($mq-mobile) {
      width: 100%;
      margin-top: 20px;
    }

    .btn {
      width: 260px;
      padding: 0;
      display: block;
    }
  }

}






























