.header {
  height: 90px;
  //position: fixed;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 100;
  @include breakpoint(1550px) {
    justify-content: flex-start;
  }
  @include breakpoint($mq-desktop-hd) {
    height: 70px;
  }
  @include breakpoint($mq-tablet) {
    height: 50px;
  }
  @include breakpoint($mq-tablet-small) {
    height: 45px;
  }


  & > .site-width {
    padding: 0 115px;
    width: 1400px;
    position: relative;
    background: $clr-white;
    transition: all ease 200ms;

    @include breakpoint(1800px) {
      width: 1300px;
      padding: 0 65px;
    }
    @include breakpoint(1700px) {
      width: 1200px;
      padding: 0 15px;
    }
    @include breakpoint(1550px) {
      width: calc(100% - 200px);
    }
    @include breakpoint($mq-tablet-small) {
      width: 100%;
      &:before {
        content: none;
      }
    }


    &:before {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      right: 100%;
      height: 100%;
      width: calc(50vw - 50%);
      background: $clr-white;
    }
  }

  &__logo {
    float: left;
    display: flex;
    height: 100%;
    align-items: center;

    svg {
      fill: $clr-red;
    }

    &-logo {
      margin-right: 21px;
      svg {
        height: 52px;
        @include breakpoint($mq-desktop-hd) {
          height: 40px;
        }
      }
    }
    &-sweeter {
      margin-right: 40px;
      svg {
        height: 16px;
        @include breakpoint($mq-desktop-hd) {
          height: 14px;
        }
      }
    }
  }

  nav {
    float: right;
    height: 100%;
    @include breakpoint($mq-tablet-small) {
      display: none;
    }


    a {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100%;
      float: left;
      padding: 0 25px;
      //text-shadow: 0 0 0 rgba(0,0,0,.5);
      @include breakpoint($mq-desktop-hd) {
        font-size: 14px;
      }
      @include breakpoint($mq-tablet) {
        font-size: 13px;
        padding: 0 15px;
      }

      &:last-child {
        padding-right: 0;
      }
    }
  }

  &__social {
    position: absolute;
    height: 100%;
    top: 0;
    left: 100%;
    margin-left: 20px;
    display: flex;
    justify-content: space-between;
    width: 150px;

    a {
      display: flex;
      height: 100%;
      justify-content: center;
      align-items: center;
      padding: 0 13px;
      svg {
        fill: $clr-white;
      }
    }
  }

}