.home {
  width: 100%;
  height: 50vmax;
  position: relative;
  background: $clr-red;

  @include breakpoint($mq-mobile) {
    height: 100vh;
  }

  #homeSection {
    width: 100%;
    height: 100%;
    position: absolute;
    perspective: 4000px;
  }

  .site-width {
    position: relative;
    z-index: 5;

    @include breakpoint($mq-mobile) {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
    }
  }

  &__cover {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    background: $clr-red;
    text-align: center;
    transform-style: preserve-3d;
    transform: translate3d(0,0,0);
    will-change: transform;
    transition: transform ease 200ms;

    svg {
      margin-top: 150px;
      max-width: 40%;
      max-height: calc(100% - 150px);
      fill: $clr-black;
      opacity: .34;
    }

    img {
      opacity: .34;
      width: 100%;
      height: 100%;
      transform: translateZ(50px) scale(1.1);

      @include breakpoint($mq-mobile) {
        width: auto;
        left: 50%;
        transform: translateX(-50%) translateZ(50px) scale(1.1);
      }
    }
  }

  &__heading {
    color: #FFF;
    display: block;
    position: absolute;
    width: 100%;
    left: -50px;
    bottom: 200px;
    height: 240px;
    font-weight: bold;
    padding-left: 100px;
    z-index: 10;

    @include breakpoint($mq-desktop-hd) {
      bottom: 130px;
      height: 230px;
    }
    @include breakpoint($mq-tablet-small) {
      bottom: 160px;
      height: 140px;
    }
    @include breakpoint($mq-mobile) {
      position: relative;
      bottom: -59px;
      height: 90px;
      left: -15px;
      padding-left: 25px;
    }


    &-cover {
      width: 370px;
      position: absolute;
      left: 0;
      top: 0;
      height: 100%;
      background: $clr-red;

      @include breakpoint($mq-tablet) {
        width: 320px;
      }
      @include breakpoint($mq-mobile) {
        width: 90px;
      }

      &:after {
        content: '';
        position: absolute;
        width: 10px;
        height: 10px;
        background: $clr-red;
        left: 100%;
        top: 100%;
      }
    }

    h1 {
      position: absolute;
      display: block;
      top: -50px;
      font-size: 226px;

      @include breakpoint($mq-desktop-hd) {
        top: -40px;
        font-size: 200px;
      }
      @include breakpoint($mq-tablet-small) {
        font-size: 150px;
      }
      @include breakpoint($mq-mobile) {
        font-size: 54px;
        top: -13px;
      }

      i {
        display: inline-block;
        width: 100%;
      }

      span {
        display: inline-block;
        white-space: nowrap;
        color: $clr-red;
        margin-left: 305px;
        position: relative;
        top: -152px;
        @include breakpoint($mq-desktop-hd) {
          font-size: 28px;
        }
        @include breakpoint($mq-tablet) {
          margin-left: 263px;
        }
        @include breakpoint($mq-tablet-small) {
          top: -120px;
          font-size: 22px;
          margin-left: 235px;
        }
        @include breakpoint($mq-tablet-min) {
          white-space: normal;
          top: -99px;
        }
        @include breakpoint($mq-mobile) {
          margin-left: 85px;
          top: -10px;
          font-size: 14px;
        }
      }
    }
  }

  &__text {
    position: absolute;
    height: 310px;
    width: 100%;
    bottom: 0;
    background: $clr-white;
    padding: 150px 95px 64px 340px;
    display: flex;
    justify-content: space-between;

    @include breakpoint($mq-desktop-hd) {
      padding: 100px 95px 40px 340px;
      height: 240px;
    }
    @include breakpoint(1230px) {
      width: calc(100% - 15px);
    }
    @include breakpoint($mq-tablet) {
      padding: 80px 95px 30px 300px;
      p {
        font-size: 12px;
      }
    }
    @include breakpoint($mq-tablet-small) {
      padding: 80px 90px 30px 90px;
      height: 220px;
    }
    @include breakpoint($mq-mobile) {
      position: relative;
      width: 100%;
      flex-direction: column;
      padding: 70px 15px 20px;
      height: auto;
    }

    p {
      display: inline-block;
      vertical-align: top;
      max-width: 45%;
      width: 100%;
      @include breakpoint($mq-mobile) {
        width: 100%;
        max-width: 100%;
        margin-bottom: 30px;
        &:nth-child(2) {
          margin-bottom: 0;
        }
      }
    }

    .cluster {
      &:before,
      &:after {
        background: $clr-red;
      }
      &_5 {
        right: 0;
        left: auto;
        bottom: 133px;
      }
      &_11 {
        width: 20px;
        height: 30px;
        left: 47px;
        bottom: -15px;
        @include breakpoint($mq-mobile) {
          left: -7px;
        }
        &:before {
          width: 50%;
          height: 100%;
          left: 0;
          top: 0;
        }
        &:after {
          width: 50%;
          height: 33.33%;
          left: 50%;
          top: 33.33%;
        }
      }
    }
  }

}