.coffee {
  .site-width {
    position: relative;

    &:before {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      right: calc(100% - 15px);
      height: 100%;
      width: calc(50vw - 50% + 15px);
      background: $bg-dark;
    }
    &:after {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: calc(100% - 15px);
      height: 100%;
      width: calc(50vw - 50% + 15px);
      background: $bg-grey;
    }
  }

  &__list {
    float: left;
    width: 40%;
    height: 100%;
    background: $bg-dark;
    padding-top: 118px;
    padding-right: 30px;
    padding-bottom: 160px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    h2 {
      font-weight: bold;
      font-size: 36px;
      color: $clr-white;
      display: inline-block;
      width: 100%;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      margin-bottom: 65px;
    }

    &-item {
      position: relative;
      float: left;
      width: 152px;
      max-width: 33.3%;
      height: 187px;
      cursor: pointer;

      &.active {
        pointer-events: none;

        .move-it {
          pointer-events: all;
        }
      }
    }

  }

  &__item {
    float: right;
    width: 60%;
    height: 100%;
    background: $bg-grey;
    padding: 210px 0 160px 30px;
    display: flex;

    @include breakpoint($mq-desktop-hd) {
      padding-left: 0;
    }

    &-description {
      padding: 15px 0 0 40px;
      width: calc(100% - 345px);
      .coffee {
        &__number {
          color: $clr-red;
          font-size: 32px;
          font-weight: bold;
          margin-bottom: 15px;
          display: inline-block;
          width: 100%;
          i {
            font-size: 18px;
            font-weight: normal;
          }
        }
        &__name {
          color: $clr-white;
          font-size: 36px;
          font-weight: bold;
          display: inline-block;
          width: 100%;
          margin-bottom: 50px;
        }
        &__about {
          margin-bottom: 35px;
          p {
            color: #FFF;
            margin-bottom: 15px;
            line-height: 1.6;
            //text-shadow: 0 0 0 rgba(255,255,255,.5);
          }
          .separator {
            width: 10px;
            height: 10px;
            display: block;
            background: $clr-red;
            margin-bottom: 15px;
          }
        }
        &__price {
          font-size: 24px;
          font-weight: bold;
          color: $clr-white;
          margin-bottom: 30px;
        }
      }
    }
  }


  &-with-slider {
    transform: translateX(0);
    transition: transform ease 400ms;
    .site-width {
      width: 100%;
      padding: 90px 0 0;
      background: $bg-dark;

      h2 {
        font-size: 18px;
        font-family: $font;
        color: $clr-white;
        font-weight: bold;
        display: inline-block;
        width: 100%;
        padding: 0 15px;
        margin-bottom: 25px;

        .toggle-coffee-list {
          float: right;
          transition: transform ease 600ms;
        }
      }
      &:before,
      &:after {
        content: none;
      }
    }
    .coffee__list {
      position: absolute;
      width: 100vw;
      left: 100%;
      top: 0;
      padding-right: 0;
      padding-top: 130px;
      h2 {
        text-align: right;
        position: absolute;
        right: 0;
        top: 91px;
      }
      .list-item {
        transform: scale(.22);
        left: 22px;
      }
      .coffee__list-item {
        height: 140px;
      }
      .list-item__id {
        left: 12px;
      }
    }
  }
}

.open-coffee-list {
  transform: translateX(-100%);
  .toggle-coffee-list {
    transform: scaleX(-1) translateX(-60px);
  }
}

.coffee__item--mobile {
  position: relative;
  width: 100%;

  .coffee-about {
    display: none;
    padding: 25px 15px;
    background: $bg-grey;

    .separator {
      width: 5px;
      height: 5px;
      background: $clr-red;
      margin-bottom: 20px;
    }
    p {
      color: $clr-white;
      margin-bottom: 20px;
      font-size: 12px;
    }

    @include breakpoint($mq-mobile) {
      display: block;

    }
  }
}

.coffee-slide {
  display: flex;
  .coffee__list-item {
    width: 50%;
    max-width: 50%;
    height: auto;
    @include breakpoint($mq-mobile) {
      height: 300px;
    }
    @include breakpoint($mq-mobile-min) {
      height: 245px;
    }

    .list-item {
      position: relative;
      transform: none;
      left: -20px;
      top: 0;
      padding-top: 1%;

      @include breakpoint($mq-mobile) {
        position: absolute;
        left: 15px;
        top: 5px;
        padding-top: 0;
        transform: scale(.5);
      }
      @include breakpoint($mq-mobile-min) {
        transform: scale(.4);
      }

      &__id {
        position: absolute;
        width: 40%;
        height: 15%;
        background: $clr-red;
        padding: 0 15px;
        font-size: 20px;
        font-weight: bold;
        display: flex;
        align-items: center;
        &>span {
          left: 0;
          top: 0;
        }
        @include breakpoint($mq-mobile) {
          width: 70px;
          height: 44px;
          font-size: 13px;
        }
        &:before {
          content: none;
        }
      }
    }
  }
  .coffee-slider-info {
    width: 50%;
    padding: 0 15px;
    @include breakpoint($mq-mobile) {
      padding-left: 0;
    }

    .coffee__name {
      width: 100%;
      ///text-align: center;
      padding: 0;
      margin-top: 10%;
      font-size: 30px;
      letter-spacing: 1.5px;
      margin-bottom: 10%;
      span.coffee__number {
        color: $clr-red;
        font-size: 50px;
        margin-right: 10px;
        i {
          font-size: 20px;
        }
      }
      @include breakpoint($mq-mobile) {
        font-size: 16px;
        text-align: center;
        margin-bottom: 20px;
        margin-top: 30%;
        span.coffee__number {
          font-size: 24px;
          margin-right: 0;
          i {
            font-size: 14px;
          }
        }
      }
    }

    .coffee__about {

      @include breakpoint($mq-mobile) {
        display: none;
      }

      .separator {
        background: $clr-red;
        width: 5px;
        height: 5px;
        margin-bottom: 20px;
      }

      p {
        color: $clr-white;
        margin-bottom: 20px;
      }
    }

    .coffee__price {
      color: $clr-white;
      font-family: $font;
      font-weight: bold;
      font-size: 40px;
      margin-top: 45px;
      margin-bottom: 25px;
      width: 100%;
      @include breakpoint($mq-mobile) {
        margin-top: 0;
        font-size: 24px;
        text-align: center;
      }
    }

    .btn.btn-buy {
      height: 43px;
      line-height: 43px;
      width: 120px;
      padding: 0;
      font-size: 14px;
      left: 50%;
      margin-left: -60px;
    }

  }
}


.landing-area {
  float: left;
  width: 345px;
  height: 590px;
  position: relative;
  .list-item {
    transform: translate3d(0,0,0);
  }
}

.list-item {
  position: absolute;
  text-align: center;
  left: 12px;
  top: 5px;
  transform-origin: left top;
  transform: scale(.263);
  will-change: transform;
  transition: transform ease 500ms;

  //.coffee__list-item:hover & {
  //  transform: translateX(-6px) translateY(-10px) scale(.3);
  //}

  & > * {
    position: absolute;
  }
  .tilter__figure {
    & > * {
      position: absolute;
    }
    .list-item__img {
      position: relative;
    }
  }

  &.temp-item {
    opacity: 0;
    transition: opacity ease 400ms;
    img {
      transform: scale(.8);
      transition: transform ease 400ms;
    }
    &.show {
      opacity: 1;
      img {
        transform: scale(1);
      }
    }
  }

  .active & {
    z-index: 30;
  }

  svg {
    width: 58px;
  }

  &__id {
    font-size: 12px;
    color: $clr-white;
    font-weight: bold;
    display: block;
    width: calc(100% - 14px);
    height: calc(100% - 25px);
    position: relative;

    span {
      display: block;
      position: relative;
      top: 16px;
      left: -2px;
    }
    &:before {
      content: '';
      display: block;
      position: absolute;
      width: 70px;
      height: 44px;
      left: -12px;
      top: 0;
      background: $clr-red;
      z-index: 0;
      transform: scale(.4, .2);
      transition: transform ease 200ms;
    }

    .coffee__list-item.active &,
    .coffee__list-item:hover & {
        &:before {
          transition: transform ease 200ms;
          transform: scale(1, 1) translateX(0) translateY(0);
        }
    }
    //.coffee__list-item:hover & {
    //  &:before {
    //    transition: transform ease 200ms;
    //    transform: scale(.5, .5) translateX(0) translateY(0);
    //  }
    //}
  }

  &__img {
    position: relative;
  }
  &__logo {
    left: 50%;
    margin-left: -16px;
    top: 213px;
    transition: all ease 400ms;
  }
  &__sweeter {
    left: 50%;
    margin-left: -16px;
    top: 309px;
    transition: all ease 400ms;
  }
  &__togo {
    width: 100%;
    text-align: center;
    padding-left: 29px;
    top: 323px;
    text-transform: uppercase;
    font-size: 5px;
    transition: all ease 400ms;
    i {
      font-size: 9px;
      position: relative;
      top: 1px;
      margin: 0 2px;
      transition: all ease 400ms;
    }
  }
  &__name {
    text-transform: uppercase;
    font-size: 27px;
    text-align: center;
    width: 100%;
    font-weight: 800;
    padding-left: calc(10% + 29px);
    padding-right: 10%;
    bottom: 188px;
    transition: all ease 400ms;
  }
  &__site {
    width: 100%;
    text-align: center;
    padding-left: 29px;
    bottom: 165px;
    font-size: 8px;
    font-weight: bold;
  }
  &__weight {
    width: 100%;
    text-align: center;
    padding-left: 29px;
    bottom: 147px;
    font-size: 12px;
    font-weight: 800;
    transition: all ease 400ms;
  }

}



.tilter {
  perspective: 500px;
}

.tilter * {
  pointer-events: none;
}

.tilter__figure,
.tilter__logo {
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  will-change: transform;
  transition: transform 200ms ease-out;
  &>div {
    position: absolute;
  }
}

.tilter__figure > * {
  transform: translateZ(0px); /* Force correct stacking order */
}

.tilter__figure {
  position: relative;
}












