.photo {
  padding: 100px 0 107px;
  overflow-y: hidden;
  height: 800px;
  margin-bottom: 108px;
  &:before {
    content: '';
    position: absolute;
    width: 100%;
    height: calc(100% - 107px);
    background: $bg-grey;
    top: 0;
    left: 0;
    z-index: -1;
  }
  @include breakpoint($mq-mobile) {
    padding: 0 0 270px;
    margin: 0;
    background: $bg-grey;
    height: auto;
    &:before {
      content: none;
    }
  }

  .slider-controls {
    position: absolute;
    bottom: 0;

    @include breakpoint($mq-mobile) {
      width: 100%;
      bottom: 245px;
      left: 0;
      display: flex;
      justify-content: space-between;
    }
  }


  &-slider.slider {
    width: 100%;

    @include breakpoint($mq-mobile) {
      height: 270px;
      bottom: 0;
      img {
        left: auto;
        right: auto !important;
        width: auto !important;
        height: 100%;
      }
    }

    .slide {
      justify-content: flex-start;
      position: relative;

      img {
        left: auto;
        right: calc((100% - 1200px)/2 + 1200px*0.3 + 3px);
        position: absolute;
        min-width: calc(100vw * 0.6);
      }
    }
  }

  &-article {
    width: 30%;
    float: right;
    padding-left: 55px;
    padding-top: 87px;

    p {
      color: $clr-white;
      font-family: $font;
      font-size: 16px;
      margin-bottom: 20px;
    }

    @include breakpoint($mq-mobile) {
      padding: 25px 0;
      width: 100%;
      p {
        font-size: 14px;
      }
    }

  }

}